.navbar {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding: 15px 65px;
    box-sizing: border-box;
}

.navbar__button {
    border: none;
    color: #3DF934;
    background: none;
    cursor: pointer;
    font-family: Consolas, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
}
