.chat {
    font-family: Consolas, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    padding: 0 65px 50px;
}

.chat__message{
    margin-top: 10px;
}

.chat__form {
    /*display: flex;*/
}
.chat__input {
    margin-top: 10px;
    background: none;
    border: none;
    color: #3DF934;
    resize: none;
    width: 80%;
    height: 100%;
    font-family: Consolas, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
}
.chat__input:focus {
    outline: none;
}

.chat__form-text {
    display: block;
    max-width: 80%;
    margin: 0;
    float: left;
}