.loader{
    height: 100%;
    width: 100%;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    color: #3DF934;
    font-family: Consolas, serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
}

.loader__dots {

}
.loader__dots:after {
    content: '.';
    -webkit-animation:  dotsBlink 1s steps(1,end) infinite;
    -o-animation:  dotsBlink 1s steps(1,end) infinite;
    animation: dotsBlink 1s steps(1,end) infinite;
}

@keyframes dotsBlink {
    0%{
        opacity: 1;
    }
    25% {
        text-shadow: 0.5em 0 0 #3DF934;
    }
    50% {
        text-shadow: 0.5em 0 0 #3DF934, 1em 0 0 #3DF934;

    }
    75%{
        opacity: 0;
    }
}