.login{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login__button {
    color: #3DF934;
    background-color: #0D0B09;
    border: 1px solid #3DF934;
    padding: 15px 75px;
    font-family: Consolas, serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    cursor: pointer;
}
.login__button:hover {
    background-color: #3DF934;
    color: #0D0B09;
}
.login__button:active {
    background-color: #2C9827;
}

.login__button-text:after {
    content: "_";
    -webkit-animation:  blink 1.2s steps(1,end) infinite;
    -o-animation:  blink 1.2s steps(1,end) infinite ;
    animation: blink 1.2s steps(1,end) infinite;
}

@keyframes blink {
    0%{
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}